import React, {FC} from 'react';
import s from './Friends.module.css';
import FriendsItem from "./friendsItem/friendsItem";
import {NavLink} from "react-router-dom";
import {sidebarType} from "../../../Redux/profile-reducer";

type PropsType={
    sidebar:Array <sidebarType>
}

const Friends:FC <PropsType> = ({sidebar}) => {

    let friendsElements = sidebar.map(f => <FriendsItem id={f.id} key={f.id} name={f.name} src={f.src}/>)

    return <div className={s.friends}>
        <div className={s.header}>
            <div>
                <h2>Friends</h2>
            </div>
            <div className={s.addNew + " " + s.active}>
                <NavLink to="/users">Add New</NavLink>
            </div>
        </div>
        <div className={s.content}>
            {friendsElements}
        </div>
    </div>
}

export default Friends;
