import React, {FC} from 'react';
import UserProfile from "./UserProfile/UserProfile";
import {ProfilePropsTypes} from "./ProfileContainer";
import MyProfileMemorized from "./MyProfile/MyProfile";


type OwnPropsType={
    isOwner:boolean
}

const Profile: FC<ProfilePropsTypes & OwnPropsType> = ({
                                    profile, updateStatus, status, smallIcons,
                                    updateStatusProfile, saveProfile, isOwner, sidebar, lifeEvent,
                                    postsName, savePhoto, socialIcons,addPostActionCreator
                                }) => {

    return (
        <div>
            <UserProfile profile={profile}
                         status={status} updateStatus={updateStatus}
                         isOwner={isOwner} savePhoto={savePhoto}
                         saveProfile={saveProfile} updateStatusProfile={updateStatusProfile}
                         socialIcons={socialIcons}
            />
            <MyProfileMemorized profile={profile} postsName={postsName} smallIcons={smallIcons}
                       addPostActionCreator={addPostActionCreator} lifeEvent={lifeEvent} sidebar={sidebar}
            />
        </div>
    )
}
export default Profile;

