import React from 'react';
import './App.css';
import {HashRouter, Route, withRouter} from "react-router-dom";
import HeaderContainer from "./components/Header/HeaderContainer";
import ProfileContainer from "./components/Profile/ProfileContainer";
import Login from "./components/Login/Login";
import {compose} from "redux";
import {connect, Provider} from "react-redux";
import {initializeApp} from "./Redux/app-Reducer";
import Preloader from "./components/common/Preloader/Preloader";
import store, {appStateType} from "./Redux/redux-store";
import {withSuspense} from "./components/hoc/withSuspense";
import NavbarContainer from "./components/Navbar/NavbarContainer";
import OnlineContainer from "./components/Online/OnlineContainer";
import UsersContainer from "./components/Users/UsersContainer";


const Dialogs = React.lazy(() => import("./components/Dialogs/Dialogs"));
//import Dialogs from "./components/Dialogs/Dialogs.tsx";


type MapPropsType = ReturnType<typeof mapStateToProps>
type DispatchPropsType = { initializeApp: () => void }

class App extends React.Component <MapPropsType & DispatchPropsType> {

    catchAllUnhandleError = (event: PromiseRejectionEvent) => {
        return alert("Внимание: Необработанная ошибка Promise: " + event.reason);
    }

    componentDidMount() {
        this.props.initializeApp();

        window.addEventListener("unhandledrejection", this.catchAllUnhandleError);
    }

    componentWillUnmount() {
        window.removeEventListener("unhandledrejection", this.catchAllUnhandleError);
    }


    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }

        return (
            <div className='app-wrapper'>
                <HeaderContainer/>
                <NavbarContainer/>
                <OnlineContainer/>
                <div className='app-wrapper-content'>
                    <Route path='/profile/:userId?' render={() => <ProfileContainer/>}/>
                    <Route path='/dialogs' render={withSuspense(Dialogs)}/>
                    <Route path='/users' render={() => <UsersContainer/>}/>
                    <Route path='/login' render={() => <Login/>}/>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state: appStateType) => {
    return {
        initialized: state.app.initialized
    }
}

const AppContainer = compose<React.ComponentType>(
    withRouter,//hoc-функция к-я возвращает другую функцию/контейнурную компоненту снабдив принявшую компоненту данными/возможност
    connect(mapStateToProps, {initializeApp})
)(App);


const SaitJSApp: React.FC = () => {
    return <HashRouter>
        <Provider store={store}>
            <AppContainer/>
        </Provider>
    </HashRouter>
}

export default SaitJSApp;