import React from 'react';
import Online from "./Online";
import {connect} from "react-redux";
import {appStateType} from "../../Redux/redux-store";
import {onlinePersonType} from "../../Redux/onlineReducer";


type PropsType={
    onlinePerson:Array <onlinePersonType>
}

  class OnlineContainer extends React.Component <PropsType> {
     render() {
         return <div>
             <Online onlinePerson={this.props.onlinePerson}/>
         </div>
     }
 }

type mapStatePropsType={onlinePerson: Array <onlinePersonType>}

const mapStateToProps = (state:appStateType):mapStatePropsType => {
    return {
        onlinePerson: state.online.onlinePerson
    }
}


export default connect (mapStateToProps, {})(OnlineContainer);

