import React, {FC} from 'react';
import s from '../Profile.module.css';
import Tabs from "../Tabs/Tabs";
import Items from "../Items/Items";
import Events from "../Event/Event";
import CreatePost from "../CreatePost/CreatePost";
import {
    lifeEventType,
    PostsnameType,
    sidebarType,
    smallIconsType,
    socialIconsType
} from "../../../Redux/profile-reducer";
import {profileType} from "../../../type/type";
import Friends from "../Friends/Friends";


type PropsType = {
    addPostActionCreator: (newPost: string) => void
    profile: profileType
    postsName: Array<PostsnameType>
    smallIcons: Array<smallIconsType>
    lifeEvent: Array<lifeEventType>
    sidebar:Array <sidebarType>
}
const MyProfile: FC<PropsType>=({addPostActionCreator,lifeEvent,postsName,
                                profile,sidebar,smallIcons,}) => {

    return (
        <div className={s.profile}>
            <Tabs />
            <Items/>
            <CreatePost addPostActionCreator={addPostActionCreator}
                        profile={profile} postsName={postsName}
                        smallIcons={smallIcons}/>
            <Events lifeEvent={lifeEvent}/>
            <Friends sidebar={sidebar}/>
        </div>
    )
}

const MyProfileMemorized=React.memo(MyProfile)

export default MyProfileMemorized


