import React from 'react';
import {connect} from "react-redux";
import Navbar from "./Navbar";
import {navbarItemsType} from "../../Redux/navbarReducer";
import {appStateType} from "../../Redux/redux-store";

type PropsType={
    navbarItems: Array <navbarItemsType>
}

class NavbarContainer extends React.Component<PropsType> {
    render() {
        return <div>
            <Navbar navbarItems={this.props.navbarItems}/>
        </div>
    }
}

type mapStateToPropsType= { navbarItems: Array <navbarItemsType>}
const mapStateToProps = (state:appStateType):mapStateToPropsType => {
    return {
        navbarItems: state.navbar.navbarItems
    }
}

export default connect(mapStateToProps, {})(NavbarContainer);

