import React from 'react';
import s from './Tabs.module.css';
import {NavLink} from "react-router-dom";

const Tabs = () => {
    return <div className={s.tabs}>
        <NavLink className={s.tab + " " + s.active} to="/dialogs">Dialogs</NavLink>
        <NavLink className={s.tab + " " + s.active} to="/users">Friends</NavLink>
        <NavLink className={s.tab + " " + s.active} to="/login">Login</NavLink>
        <NavLink className={s.tab + " " + s.active} to="/profile/:userId?">Profile</NavLink>
    </div>
}

export default Tabs;

