import React, {FC} from 'react';
import s from './Online.module.css';
import {NavLink} from "react-router-dom";
import {onlinePersonType} from "../../Redux/onlineReducer";

type PropsType={
    onlinePerson:Array <onlinePersonType>
}
const Online:FC <PropsType> = ({onlinePerson}) => {

    const arrayOnline = onlinePerson.map(n => {
        return <div key={n.id}>
            <NavLink to={n.urlName}><img
                src={n.src}/>
                <h6>{n.h6}</h6>
            </NavLink>
        </div>
    })

    return <div className={s.online}>
        {arrayOnline}
    </div>
}

export default Online;