import React, {FC} from 'react';
import s from './Navbar.module.css';
import {NavLink} from "react-router-dom";
import {navbarItemsType} from "../../Redux/navbarReducer";

type PropsType = {
    navbarItems: Array<navbarItemsType>
}

const Navbar: FC<PropsType> = ({navbarItems}) => {

    const ArrayNavbar = navbarItems.map(n => {
        return <div key={n.id}>
            <NavLink to={n.urlName}><img src={n.src} className={s.active}/></NavLink>
        </div>
    })

    return <nav className={s.nav}>
        {ArrayNavbar}
    </nav>
}
export default Navbar;