import React from "react";
import Header from "./Header";
import {connect} from "react-redux";
import {logout} from "../../Redux/authReducer";
import {profileType} from "../../type/type";
import {ArrIconsHeaderType} from "../../Redux/headerReducer";
import {appStateType} from "../../Redux/redux-store";

type PropsType=MapStatePropsType & MapDispatchPropsType

class HeaderContainer extends React.Component<PropsType> {
    render() {
        return <Header {...this.props} />
    }
}

type MapStatePropsType={isAuth: boolean
    login: string|null
    profile: profileType|null
    ArrIconsHeader: Array <ArrIconsHeaderType>}
type MapDispatchPropsType={logout:any}

const mapStateToProps = (state:appStateType):MapStatePropsType => ({
    isAuth: state.auth.isAuth,
    login: state.auth.login,
    profile: state.postPage.profile,
    ArrIconsHeader: state.header.ArrIconsHeader,
});


export default connect (mapStateToProps, {logout})(HeaderContainer);

