import React, {FC} from 'react';
import s from './../Friends.module.css'

type PropsType={id:number,src:string, name:string}

const FriendsItem:FC <PropsType> = ({id,src,name}) => {

    return <div className={s.content}>
        <div className={s.icons} key={id}>
            <div className={s.ava}>
                <a href='#'> <img src={src}/></a>
            </div>
            <div className={s.names}>
                <h3>{name}</h3>
            </div>
        </div>
    </div>
}

export default FriendsItem;


