import React, {FC} from 'react';
import s from './CreatePost.module.css';
import Post from './Postt/Post'
import {AddNewPostFormValuesType, AddPostReduxForm} from "./AddPostForm/AddNewPost";
import MyPhotoNavlink from "../../common/MyPhotoNavlink/MyPhotoNavlink";
import {profileType} from "../../../type/type";
import {PostsnameType, smallIconsType} from "../../../Redux/profile-reducer";

type PropsType= {
    profile: profileType
    postsName: Array<PostsnameType>
    smallIcons: Array <smallIconsType>
    addPostActionCreator: (newPost: string) => void
}


const CreatePost:FC <PropsType> = ({profile, postsName,
                                       smallIcons, addPostActionCreator}) => {


    const arrSmallIcons = smallIcons.map(s => {
        return <div key={s.id}>
            <img src={s.src}/>
            <p>{s.p}</p>
        </div>
    })

    let newPost = (values:AddNewPostFormValuesType) => {
        addPostActionCreator(values.newPost)
    };


    let postsElements = postsName.map(p => <Post key={p.id}
                                                 id={p.id}
                                                 src1={p.src1}
                                                 name={p.name}
                                                 name1={p.name1}
                                                 name2={p.name2}
                                                 src2={p.src2}
                                                 p3={p.p3}
                                                 src3={p.src3}
                                                 src4={p.src4}
                                                 span1={p.span1}
                                                 time1={p.time1}
                                                 src5={p.src5}
                                                 text1={p.text1}
                                                 src6={p.src6}
                                                 text2={p.text2}
                                                 text3={p.text3}
                                                 span2={p.span2}
                                                 src7={p.src7}
                                                 text4={p.text2}
                                                 text5={p.text3}
                                                 span3={p.span2}
    />)

    return <div className={s.posts}>
        <div className={s.createPost}>
            <div className={s.header}>
                <h3>Create Post</h3>
            </div>
            <div className={s.content_ava}>
                <div className={s.content1}>
                    <MyPhotoNavlink profile={profile}/>
                </div>
                <AddPostReduxForm onSubmit={newPost}/>
            </div>
            <div className={s.footerCreatePost}>
                {arrSmallIcons}
            </div>
        </div>
        <div className={s.post}>
            {postsElements}
        </div>
    </div>
}


export default CreatePost;