
import {appStateType, InferActionTypes} from "./redux-store";
import {Dispatch} from "redux";
import {ThunkAction} from "redux-thunk";
import {usersType} from "../type/type";
import {UsersAPI} from "../API/users-api";
import {ResultCodeEnum} from "../API/API";
import {updateObjectArray} from "../utils/object_helper/object_helper";

let initialState = {
    users: [
        {
            name: 'restdz',
            id: 1,
            uniqueUrlName: null,
            photos: {
                small: null,
                large: null
            },
            status: null,
            followed: true
        },
        {
            name: 'Vova342',
            id: 2,
            uniqueUrlName: null,
            photos: {
                small: null,
                large: null
            },
            status: null,
            followed: false
        },
        {
            name: 'test12344',
            id: 3,
            uniqueUrlName: null,
            photos: {
                small: null,
                large: null
            },
            status: null,
            followed: false
        },
        {
            name: 'raxani6032',
            id: 4,
            uniqueUrlName: null,
            photos: {
                small: null,
                large: null
            },
            status: null,
            followed: false
        },
        {
            name: 'Monika',
            id: 5,
            uniqueUrlName: null,
            photos: {
                small: null,
                large: null
            },
            status: null,
            followed: false
        },
        {
            name: 'test12344',
            id: 6,
            uniqueUrlName: null,
            photos: {
                small: null,
                large: null
            },
            status: null,
            followed: false
        }
    ] as Array<usersType>,
    pageSize: 10,
    totalItemsCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: [] as Array<number>,
}

export type initialStateType = typeof initialState

const usersReducer = (state = initialState, action: ActionType): initialStateType => {
    switch (action.type) {
        case 'FOLLOW':
            return {
                ...state,
                users: updateObjectArray(state.users, action.userId, "id", {followed: true})
            }
        case 'UNFOLLOW':
            return {
                ...state,
                users: updateObjectArray(state.users, action.userId, "id", {followed: false})
            }
        case 'SET_USERS': {
            return {...state, users: action.users}
        }
        case 'SET_CURRENT_PAGE': {
            return {...state, currentPage: action.page}
        }
        case 'SET_TOTAL_USERS_COUNTS': {
            return {...state, totalItemsCount: action.totalItemsCount}
        }
        case 'TOGGLE_IS_FETCHING': {
            return {...state, isFetching: action.isFetching}
        }
        case 'TOGGLE_FOLLOWING_PROGRESS': {
            return {
                ...state, followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id != action.userId)
            }
        }
        default:
            return state;
    }
}

type ActionType = InferActionTypes<typeof actions>

export const actions = {
    followSuccess: (userId: number) => ({type: 'FOLLOW', userId} as const),
    unfollowSuccess: (userId: number) => ({type: 'UNFOLLOW', userId} as const),
    setUsers: (users: Array<usersType>) => ({type: 'SET_USERS', users} as const),
    setCurrentPage: (page: number) => ({type: 'SET_CURRENT_PAGE', page} as const),
    setTotalUsersCount: (totalItemsCount: number) => ({
        type: 'SET_TOTAL_USERS_COUNTS', totalItemsCount
    } as const),
    toggleIsFetching: (isFetching: boolean) => ({type: 'TOGGLE_IS_FETCHING', isFetching} as const),
    toggleFollowingInProgress: (isFetching: boolean, userId: number) => ({
        type: 'TOGGLE_FOLLOWING_PROGRESS',
        isFetching,
        userId
    } as const)
}

type DispatchType = Dispatch<ActionType>
type ThunkType = ThunkAction<Promise<void>, appStateType, unknown, ActionType>

export const requestUsers = (page: number,
                             pageSize: number): ThunkType => {
    return async (dispatch) => {
        dispatch(actions.toggleIsFetching(true));
        dispatch(actions.setCurrentPage(page));
        let data = await UsersAPI.getUsers(page, pageSize);
        dispatch(actions.setTotalUsersCount(data.totalCount));
        dispatch(actions.toggleIsFetching(false));
        dispatch(actions.setUsers(data.items));// результат вызова action creator
    }
}

export const onPageChanged = (page: number, pageSize: number): ThunkType => {
    return async (dispatch) => {
        dispatch(actions.toggleIsFetching(true));
        dispatch(actions.setCurrentPage(page));
        let data = await UsersAPI.getUsers(page, pageSize);
        dispatch(actions.toggleIsFetching(false));
        dispatch(actions.setUsers(data.items));
    }
}

const followUnfollow = async (dispatch: DispatchType, userId: number,
                              apiMethod: any, actionCreator: (userId: number) => ActionType) => {
    dispatch(actions.toggleFollowingInProgress(true, userId));
    let data = await apiMethod(userId);
    if (data.resultCode == ResultCodeEnum.Success) {
        dispatch(actionCreator(userId));
    }
    dispatch(actions.toggleFollowingInProgress(false, userId));
}

export const follow = (userId: number): ThunkType => {
    return async (dispatch) => {
        followUnfollow(dispatch, userId, UsersAPI.follow.bind(UsersAPI), actions.followSuccess)
    }
}

export const unfollow = (userId: number): ThunkType => {
    return async (dispatch) => {
        followUnfollow(dispatch, userId, UsersAPI.unfollow.bind(UsersAPI), actions.unfollowSuccess)
    }
}

export default usersReducer;