import React, {FC} from "react";
import {NavLink} from "react-router-dom";
import userPhoto from "../../../axios/images/user.png";
import s from "./MyPhotoNavlink.css";
import {profileType} from "../../../type/type";

type PropsType={
    profile:profileType
}

const MyPhotoNavlink:FC <PropsType> = ({profile}) => {
    return <div>
        <NavLink to="/profile">
            <img src={profile != null ? profile.photos.large : userPhoto} className={s.photo}/>
        </NavLink>
    </div>
}
export default MyPhotoNavlink;