import React, {ChangeEvent, FC, useEffect, useState} from "react";

type PropsType={
    status:string
    updateStatus:(newStatus:string)=>void
}


const ProfileStatusWithHooks: FC <PropsType> = ({status,updateStatus}) => {

    let [editMode, setEditMode] = useState<boolean>(false);//локальный state- внутри массив: 0 элемент state, а 1 эл.массива изменения лок.state
    let [localStateStatus, setStatus] = useState<string>(status);//инциализилованное значения сначало будет
    // из глобального состояние из props придет,а дальше измениться на локальное состояние с помощью setState

    useEffect(() => {
        setStatus(status)
    }, [status])
    //зависимости должны быть, что бы мы могли знать когда нужно запустить функцию, изменить локальный стейт на актуальные данные
    //запускайся каждый раз, если при каждом render() рание props.status не будет равен к новому пришедшему props.status из BLL
    //useEffect -используем для синхронизации глобального состояния в локальное state,
    // с помощью значении зависимости мы можем проверить изменился ли в глобальное state,
    // если изменился запускаем setState что бы изменить и локальный state и в итоге отрисуется в статусе актуальный state


    const activateEditMode = () => {
        setEditMode(true)
    }
    const deactivateEditMode = () => {
        setEditMode(false)
        updateStatus(localStateStatus);
    }
    const onStatusChange = (e:ChangeEvent<HTMLInputElement>) => {
        setStatus(e.currentTarget.value)
    }

    return (
        <>
            <div>
                {!editMode && <div>
                    <span onDoubleClick={activateEditMode}>{localStateStatus || '------'}</span>
                </div>}
            </div>
            <div>
                {editMode && <div>
                    <input onChange={onStatusChange} autoFocus={true} onBlur={deactivateEditMode}
                           value={localStateStatus}/>
                </div>}
            </div>
        </>
    )
}

export default ProfileStatusWithHooks;